.skills-content {
  background-color: #141117;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 0px;
  overflow-y: auto;
}

.content-heading {
  font-size: 34px;
  text-align: center;
  color: #fff;
  line-height: 38px;
  font-weight: 300;
}

.skills-card-wrapper {
    padding: 20px 20px 10px 20px;
    background-color: #fff;
    margin: 0 auto;
    width: 90%;
    margin-top: 40px;
    border-radius: 4px;
}

.font-light {
    font-weight: 300;
}

.skills-card-wrapper h4 {
    color: #333;
    border-bottom: 2px solid #ddd;
    margin-bottom: 30px;
    padding-bottom: 15px;
    font-size: 21px;
}

.skills-card {
    color: #333;
    font-size: 16px;
    position: relative;
    min-height: 130px;
    margin-bottom: 30px;
}

.skills-card img {
    max-height: 85px;
    margin: 0 auto;
    margin-bottom: 8px;
}

.skill-text {
    position: absolute;
    bottom: 0px;
    right: 0px;
    left: 0px;
}

.p-b-40 {
    padding-bottom: 40px;
}