.side-nav {
  background-color: #141117;
  height: 100vh;
  text-align: center;
  padding: 0px;
  display: flex;
  position: fixed;
}

.side-nav ul {
  padding: 0px;
  margin: 0px;
  display: flex;
  height: 100vh;
  flex-direction: column;
  width: 100%;
  min-height: 600px;
}

.side-nav-item {
  flex: 1;
  color: #fff;
  list-style: none;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.side-nav-item a {
  color: #fff;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  height: 100%;
  width: 100%;
}

.side-nav-item a.active {
  background-color: #08fdd8;
  transition: 0.2s ease-in-out;
}

.side-nav-item.active:hover {
  color: #fff;
}

.side-nav-item:hover {
  color: #08fdd8;
  transition: 0.2s ease-in-out;
}

.nav-item-icon {
  font-size: 25px;
}
