body {
  font-size: 14px;
  background-color: #141117;
  font-family: "Comic Sans MS", cursive, Helvetica, sans-serif;
}

.terminal-base {
  width: 100% !important;
  max-width: 100% !important;
}

.terminal-base div {
  width: 100% !important;
  max-width: 100% !important;
  overflow: hidden !important;
}

.terminal-base pre {
  background-color: unset !important;
  border: unset !important;
  margin: 0px !important;
  padding: 5px 8px !important;
}

.vertical-timeline-element-icon i {
  display: block;
  position: absolute;
  top: calc(50% - 23px);
  left: 0px;
  right: 0px;
  text-align: center;
  line-height: 1.42857143;
}

.toggle-menu {
  height: 40px;
  width: 40px;
  border-radius: 5px;
  border: 1px solid #fff;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 20;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  cursor: pointer;
}

.menu-bars {
  height: 2px;
  width: 26px;
  background-color: #fff;
}

.desktop-side-nav {
  display: block;
}

.mobile-side-nav {
  display: none;
}

@media (max-width: 991px) {
  .toggle-menu {
    display: flex;
  }
  .mobile-side-nav {
    display: block;
  }
  .mobile-side-nav .side-nav {
    position: relative;
    height: auto;
  }
  .desktop-side-nav {
    display: none;
  }
}
