body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-size: 14px;
  background-color: #141117;
  font-family: "Comic Sans MS", cursive, Helvetica, sans-serif;
}

.terminal-base {
  width: 100% !important;
  max-width: 100% !important;
}

.terminal-base div {
  width: 100% !important;
  max-width: 100% !important;
  overflow: hidden !important;
}

.terminal-base pre {
  background-color: unset !important;
  border: unset !important;
  margin: 0px !important;
  padding: 5px 8px !important;
}

.vertical-timeline-element-icon i {
  display: block;
  position: absolute;
  top: calc(50% - 23px);
  left: 0px;
  right: 0px;
  text-align: center;
  line-height: 1.42857143;
}

.toggle-menu {
  height: 40px;
  width: 40px;
  border-radius: 5px;
  border: 1px solid #fff;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 20;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  cursor: pointer;
}

.menu-bars {
  height: 2px;
  width: 26px;
  background-color: #fff;
}

.desktop-side-nav {
  display: block;
}

.mobile-side-nav {
  display: none;
}

@media (max-width: 991px) {
  .toggle-menu {
    display: flex;
  }
  .mobile-side-nav {
    display: block;
  }
  .mobile-side-nav .side-nav {
    position: relative;
    height: auto;
  }
  .desktop-side-nav {
    display: none;
  }
}

.side-nav {
  background-color: #141117;
  height: 100vh;
  text-align: center;
  padding: 0px;
  display: flex;
  position: fixed;
}

.side-nav ul {
  padding: 0px;
  margin: 0px;
  display: flex;
  height: 100vh;
  flex-direction: column;
  width: 100%;
  min-height: 600px;
}

.side-nav-item {
  flex: 1 1;
  color: #fff;
  list-style: none;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.side-nav-item a {
  color: #fff;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  height: 100%;
  width: 100%;
}

.side-nav-item a.active {
  background-color: #08fdd8;
  transition: 0.2s ease-in-out;
}

.side-nav-item.active:hover {
  color: #fff;
}

.side-nav-item:hover {
  color: #08fdd8;
  transition: 0.2s ease-in-out;
}

.nav-item-icon {
  font-size: 25px;
}

.main-content {
  background-color: #141117;
  background-image: url(/assets/images/hitesh-bg.png);
  background-size: contain;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 0px;
}

/* About Me page 
  ====================================== */

.content-wrapper {
  display: flex;
  flex-direction: column;
  margin-right: 10%;
  flex: 1 1;
  justify-content: center;
  align-items: flex-end;
}

.content-name {
  margin-bottom: 0px;
  font-size: 70px;
  padding: 0px;
  color: #08fdd8;
  text-align: right;
  margin-top: 10%;
}

.content-designation {
  color: #fff;
  font-size: 18px;
  margin-top: 0px;
  text-align: right;
  padding: 0px;
  margin-bottom: 20px;
}

.social-media-icon {
  height: 40px;
  width: 40px;
  border-radius: 40px;
  line-height: 40px;
  text-align: center;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  float: right;
  margin-left: 10px;
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
  transition: 0.2s ease-in-out;
}

.social-media-icon:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  transition: 0.2s ease-in-out;
}

.social-media-icon.fb {
  background-color: #4867a9;
}

.social-media-icon.in {
  background-color: #0677b4;
}

.social-media-icon.git {
  background-color: #23292d;
  font-size: 40px;
}

.social-media-icon.tw {
  background-color: #21a1f0;
}

.social-media-icon.plus {
  background-color: #da4437;
}

.social-media-icon.insta {
  background-color: #fff;
  color: #000;
  font-size: 25px;
}

.social-media-icon.skype {
  background-color: #09aeee;
  font-size: 25px;
}

@media (max-width: 768px) {
  .main-content {
    background-image: none;
  }
  .content-wrapper {
    margin: 0px;
    padding: 0px;
    text-align: center;
    align-items: center;
  }
  .content-name {
    text-align: center;
  }
  .content-designation {
    text-align: center;
  }
  .social-media-wrapper {
    display: flex;
    justify-content: center;
  }
  .social-media-icon {
    margin-left: 5px;
  }
}

