.project-content {
  background-color: #141117;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 0px;
  overflow-y: auto;
}

:global(.vertical-timeline-element-date) {
  font-size: 16px !important;
  color: #fff !important;
}

:global(.vertical-timeline-element-subtitle) {
  margin: 10px 0px;
}

:global(.vertical-timeline-element-content p) {
  font-size: 13px;
}

@media only screen and (min-width: 1170px) {
  .vertical-timeline--two-columns
    .vertical-timeline-element-content
    .vertical-timeline-element-date {
    left: 140%;
  }

  .vertical-timeline--two-columns
    .vertical-timeline-element:nth-child(even):not(.vertical-timeline-element--left)
    .vertical-timeline-element-content
    .vertical-timeline-element-date {
    right: 140%;
  }
}

:global(.vertical-timeline) {
  margin-top: 0px !important;
  margin-bottom: 0px !important;
  padding: 40px 0px 50px 0px;
  height: 100%;
}

:global(.vertical-timeline::before) {
    height: 100%;
}

.vertical-timeline-element-content {
    margin-bottom: 50px;
}

.content-heading {
  font-size: 34px;
  text-align: center;
  color: #fff;
  line-height: 38px;
  font-weight: 300;
}

.content-description {
  text-align: center;
  color: #fff;
}

.project-border .vertical-timeline-element-content {
  border-top-width: 4px;
  border-top-style: solid;
}

.react-color .vertical-timeline-element-content {
  border-top-color: #61dafb;
}

.angular-color .vertical-timeline-element-content {
  border-top-color: #e43136;
}

.php-color .vertical-timeline-element-content {
  border-top-color: #777CB5;
}

.tools-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-bottom: 15px;
}

.tools-capsule {
  height: 32px;
  line-height: 32px;
  background-color: #E0E0E0;
  display: inline-flex;
  padding: 0px 10px;
  border-radius: 32px;
  margin-right: 10px;
}
